<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-xs-12 col-sm-12 col-md-12 col-lg-6 offset-lg-3 text-center">
        <h1>The Tanks</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12 text-center">
        WSDA - move, left click - fire, Q - switch weapon T - teleportation to mouse position
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-xs-12 mt-xs-1 col-sm-8 col-md-8 col-lg-8">
        <canvas width="600" height="400" ref="canvas" id="canvas"></canvas>
      </div>
      <div class="col-xs-12 col-sm-4 col-md-4 col-lg-4 text-left">
        <div v-if="game && game.player">
          <div>Health: {{ game.player && game.player.health }}%</div>
          <div>Enemies killed: {{ game.score }}</div>
          <div :class="{active: !index}" v-for="(weapon, index) of game.player.weapons" >
            {{weapon.name}}: <b-badge variant="success">{{weapon.ammoCount}}</b-badge>
          </div>
        </div>
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-xs-12 mt-xs-1 col-sm-12 mt-sm-1 col-md-12 col-lg-12 text-center">
        <b-button variant="primary" @click="startGame">Start game</b-button>
      </div>
    </div>
    <b-modal
      v-model="isGameOver"
      v-b-modal.modal-center
      ref="modal"
      scrollable
      :title="'Game Over'"
      :ok-title="'Close'"
      :ok-only="true"
      @ok="finishGame"
    >
      Enemies killed: {{ game && game.score }}
    </b-modal>

  </div>
</template>

<script>
    import { Game } from "canvas-game-example-the-tanks";

    export default {
        name: "Main",
        components: {
        },
        data() {
            return {
                game: null,
                isGameOver: false
            };
        },
        mounted() {

        },

        methods: {

            gameOverHandler() {
                this.isGameOver = true;
            },

            startGame() {
                const self = this;
                this.isGameOver = false;
                this.game = new Game({
                    addEnemyThresholdMs: 5000,
                    addStaticItemThresholdMs: 1000,
                    staticItemsMaxCount: 20,
                    dynamicItemsMaxCount: 2,
                    fps: 25,
                    reactionLimit: 1000,
                    gameOverHandler: this.gameOverHandler.bind(self)
                });
                this.game.init(this.$refs.canvas).start();
            },


            finishGame() {
                // this.isGameOver = false;
                // this.game = null;
            }
        }
    };
</script>

<style scoped>
  h4 {
    color: #5a626a;
  }
  p {
    padding-left: 4px;
  }

  canvas {
    border: 1px solid #aaa;
  }

  div.active {
    font-weight: bold;
  }

</style>
